<template>
  <div class="pinochle-score-analyzer container">
    <h3>Hand Analysis</h3>
    <div class="score">
      <p>Total Score: {{ calculateScore() }}</p>
      <!-- Additional analysis and breakdown can be displayed here -->
    </div>
    <!-- You can iterate over the hand and display each card with Bootstrap Icons -->
    <div class="row gap-0">
      <div class="col-1 px-1" v-for="(card, i) in hand" :key="i">
        <img
          :src="drawnCardImage(card.code)"
          alt=""
          class="img-fluid rounded shadow"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PinochleScoreAnalyzer",
  props: {
    hand: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      sortedHand: [],
    };
  },
  computed: {
    // Computed properties for analyzing the hand
  },
  created() {},
  methods: {
    // Methods for specific calculations
    calculateScore() {
      let score = 0;
      // Logic to calculate the score based on the hand
      // This might involve checking for specific combinations, sequences, etc.
      return score;
    },
    drawnCardImage(code) {
      return require(`@/assets/img/cards/${code}.png`);
    },
    // Method to sort the hand by suit and rank
    sortHand() {
      const spades = this.hand
        .filter((card) => card.suit === "Spades")
        .sort((a, b) => a.pinochleValue - b.pinochleValue);
      this.sortedHand.push(...spades);
      const hearts = this.hand
        .filter((card) => card.suit === "Hearts")
        .sort((a, b) => a.pinochleValue - b.pinochleValue);
      this.sortedHand.push(...hearts);
      const diamonds = this.hand
        .filter((card) => card.suit === "Diamonds")
        .sort((a, b) => a.pinochleValue - b.pinochleValue);
      this.sortedHand.push(...diamonds);
      const clubs = this.hand
        .filter((card) => card.suit === "Clubs")
        .sort((a, b) => a.pinochleValue - b.pinochleValue);
      this.sortedHand.push(...clubs);
    },
  },
};
</script>

<style scoped>
.playing-card {
  width: 100px;
  height: 150px;
  margin: 5px;
}
</style>
